<template>
  <div @scroll.native="scrollHandler" class="navbar" :style="{'background': bgColor}">
    <div class="navbar-inner">
      <span class="navbar-inner-logo">
        <img src="../assets/img/logo.png">
      </span>
      <ul class="navbar-inner-list">
        <li v-for="(item,index) in items" 
            :key="index" 
            :class="{'navbar-inner-list-item': true, 'selected': index==nowSelected, 'border': item.class}"
        >
          <a :href="item.link">{{item.name}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bgColor: 'transparent',
      nowSelected: -1,
      items: [
        {id: 0, name: '报名入口', link: '#reg', class: 'true'},
        {id: 1, name: '大赛支持', link: '#sup', class: ''},
        {id: 2, name: '赛程安排', link: '#sch', class: ''},
        {id: 3, name: '参赛须知', link: '#qua', class: ''},
        {id: 4, name: 'Hackthon简介', link: '#des', class: ''},
      ]
    }
  },
  mounted() {
    window.addEventListener('scroll', this.scrollHandler)
  },
  methods: {
    scrollHandler: function (el) {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop < 200) {
        this.bgColor = "transparent"
      } else {
        this.bgColor = "black"
      }

      if (scrollTop < 300) {
        this.nowSelected = -1;
      } else if (scrollTop < 750) {
        this.nowSelected = 4;
      } else if (scrollTop < 1800) {
        this.nowSelected = 3;
      } else if (scrollTop < 2300) {
        this.nowSelected = 2;
      } else if (scrollTop < 3400) {
        this.nowSelected = 1;
      } else {
        this.nowSelected = 0;
      }
      //console.log(scrollTop)
    }
  }
}
</script>

<style scoped>
.navbar {
  z-index: 999;
  transition: background .7s;
}
</style>



